.map-h2 {
    text-transform: uppercase;
    font-size: 1rem;
    padding: 20px;
    padding-left: 10px;
    text-align: center;
  }
  
  .google-map {
    width: 100%;
    height: 60vh;
  }
  
  .pin {
    display: flex;
    align-items: center;
    width: 18px;
    color: var(--main-blue);
  }
  
  .pin-icon {
    width: 10px;
    font-size: 1rem;
  }
  
  .pin-text {
    font-size: 1.3em;
  }
  
  @media screen and (min-width: 945px) {
    .google-map {
      max-height: 40vh;
    }
  
    .map-h2 {
      font-size: 1.3rem;
      font-weight: 400;
    }
  
    .pin {
      width: 5vw;
    }
  
    .pin-icon {
      font-size: 3vw;
    }
  }